:root {
  --md-sys-color-primary: #2196f3;
  --md-sys-color-secondary: #ff4081;
  --background: #f0f0f0;
  --font: #000;
}

body {
  background: var(--background);
  color: var(--font);
  margin: 0;
  padding: 0 24px;
  font-family: Helvetica, Arial, sans-serif;
}

header, main {
  max-width: 1140px;
  margin: 24px auto 0;
}

main {
  gap: 24px;
  display: flex;
}

header {
  background-color: #e8e8e8;
  border-radius: 32px;
  height: 64px;
  position: relative;
  overflow: hidden;
}

button {
  all: unset;
  background-color: var(--md-sys-color-primary);
  color: #fff;
  cursor: pointer;
  padding: 0 12px;
}

section {
  background-color: #e8e8e8;
  border-radius: 32px;
  padding: 16px;
  display: block;
}

section input[type="number"], section input {
  all: unset;
  text-indent: 24px;
  color: #606060;
  background: #d0d0d0;
  border-radius: 24px;
  width: 100%;
  height: 48px;
  font-size: 24px;
}

label {
  color: #606060;
  margin-left: 16px;
  font-size: 16px;
}

.file-input-wrapper {
  float: left;
  cursor: pointer;
  text-align: center;
  color: #f2f2f2;
  background-color: #606060;
  border-radius: 24px;
  height: 48px;
  margin: 0 auto;
  padding: 0 64px;
  font-size: 24px;
  line-height: 48px;
  display: block;
}

.file-input-wrapper input[type="file"] {
  display: none;
}

section h2 {
  margin: 0 0 16px;
  padding: 0;
  font-size: 24px;
  line-height: 32px;
}

h2 img {
  float: left;
  height: 32px;
  margin-right: 8px;
}

.col {
  flex-direction: column;
  flex: 1;
  gap: 24px;
  display: flex;
}

#submit {
  position: inherit;
  float: right;
  z-index: 2;
  border-radius: 16px;
  height: 32px;
  margin: 16px;
}

#github-link {
  position: inherit;
  z-index: 2;
  float: left;
  background-image: url("github-brands-solid-full.a1c4b5e6.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
  margin: 16px;
  display: block;
}

#progress {
  z-index: 1;
  width: 0%;
  height: inherit;
  background-color: var(--md-sys-color-primary);
  opacity: .25;
  transition: width .1s;
  position: absolute;
  top: 0;
  left: 0;
}
